.price-up {
    color: #c84a31; /* 상승할 때 빨간색 */
    transition: color 0.3s ease, opacity 0.3s ease; /* opacity에 대한 트랜지션 추가 */
    opacity: 1; /* 기본 불투명도 */
}

.price-up:hover {
    opacity: 0.8; /* 마우스 오버 시 약간의 반짝임 효과 */
}

.price-down {
    color: #1261c4; /* 하락할 때 파란색 */
    transition: color 0.3s ease, opacity 0.3s ease; /* opacity에 대한 트랜지션 추가 */
    opacity: 1; /* 기본 불투명도 */
}

.price-down:hover {
    opacity: 0.8; /* 마우스 오버 시 약간의 반짝임 효과 */
}
